<template>
	<div id="store-home">
		<public-header :title="store.title"></public-header>
		<public-footer :menufooter="menufooter"></public-footer>
		<div class="content">
			<div class="border-1px-b ellipsis">
				<div class="store-data">
					<div class="data-url">
						<div class="font-20 ellipsis">{{stat.total_order}}</div>
						<div class="c-gray">今日有效订单</div>
					</div>
				</div>
				<div class="store-data">
					<div class="data-url">
						<div class="font-20 ellipsis">{{stat.total_fee}}</div>
						<div class="c-gray">今日总营业额</div>
					</div>
				</div>
				<div class="store-data">
					<div class="data-url">
						<div class="font-20 ellipsis">{{stat.final_fee}}</div>
						<div class="c-gray">今日总收入</div>
					</div>
				</div>
			</div>
			<div class="group border-1px-tb">
				<van-row gutter="20">
					<van-col span="8">
						<router-link tag="div" :to="util.getUrl({path: '/pages/shop/setting'})" class="merchant-item">
							<div class="top">
								<i class="icon icon-shop"></i>
							</div>
							<div class="bottom">
								<span>店铺管理</span>
							</div>
						</router-link>
					</van-col>
					<van-col span="8">
						<router-link tag="div" :to="util.getUrl({path: '/pages/order/index'})" class="merchant-item">
							<div class="top">
								<i class="icon icon-order c-info"></i>
							</div>
							<div class="bottom">
								<span>外卖订单</span>
							</div>
						</router-link>
					</van-col>
					<van-col span="8">
						<router-link tag="div" :to="util.getUrl({path: '/pages/order/tangshi/index'})" class="merchant-item">
							<div class="top">
								<i class="icon icon-meal c-info"></i>
							</div>
							<div class="bottom">
								<span>店内订单</span>
							</div>
						</router-link>
					</van-col>
					<van-col span="8">
						<router-link tag="div" :to="util.getUrl({path: '/pages/goods/index'})" class="merchant-item">
							<div class="top">
								<i class="icon icon-goods c-info"></i>
							</div>
							<div class="bottom">
								<span>商品管理</span>
							</div>
						</router-link>
					</van-col>
					<van-col span="8">
						<router-link tag="div" :to="util.getUrl({path: '/pages/service/comment'})" class="merchant-item">
							<div class="top">
								<i class="icon icon-survey c-info"></i>
							</div>
							<div class="bottom">
								<span>评论管理</span>
							</div>
						</router-link>
					</van-col>
					<van-col span="8">
						<router-link tag="div" :to="util.getUrl({path: '/pages/finance/index'})" class="merchant-item">
							<div class="top">
								<i class="icon icon-recharge c-info"></i>
							</div>
							<div class="bottom">
								<span>资产</span>
							</div>
						</router-link>
					</van-col>
					<van-col span="8">
						<router-link tag="div" :to="util.getUrl({path: '/pages/activity/index'})" class="merchant-item">
							<div class="top">
								<i class="icon icon-activity c-info"></i>
							</div>
							<div class="bottom">
								<span>店铺活动</span>
							</div>
						</router-link>
					</van-col>
					<van-col span="8" v-if="util.checkplugin('advertise')">
						<router-link tag="div" :to="util.getUrl({path: '/pages/advertise/index'})" class="merchant-item">
							<div class="top">
								<i class="icon icon-medal c-info"></i>
							</div>
							<div class="bottom">
								<span>店铺推广</span>
							</div>
						</router-link>
					</van-col>
					<van-col span="8">
						<router-link tag="div" to="/pages/news/notice" class="merchant-item">
							<div class="top">
								<i class="icon icon-newshot c-info"></i>
							</div>
							<div class="bottom">
								<span>公告</span>
							</div>
						</router-link>
					</van-col>
					<van-col span="8">
						<router-link tag="div" to="/pages/paybill/index" class="merchant-item">
							<div class="top">
								<i class="icon icon-signboard c-info"></i>
							</div>
							<div class="bottom">
								<span>买单</span>
							</div>
						</router-link>
					</van-col>
					<van-col span="8">
						<router-link tag="div" to="/pages/order/table" class="merchant-item">
							<div class="top">
								<i class="icon icon-similar c-warning"></i>
							</div>
							<div class="bottom">
								<span>桌台管理</span>
							</div>
						</router-link>
					</van-col>
					<van-col span="8">
						<router-link tag="div" to="/pages/order/assign" class="merchant-item">
							<div class="top">
								<i class="icon icon-friend_light c-danger"></i>
							</div>
							<div class="bottom">
								<span>排队</span>
							</div>
						</router-link>
					</van-col>
					<van-col span="8" v-if="util.checkplugin('gohome')">
						<router-link tag="div" :to="util.getUrl({path: '/pages/gohome/index'})" class="merchant-item">
							<div class="top">
								<i class="icon icon-home c-danger"></i>
							</div>
							<div class="bottom">
								<span>生活圈</span>
							</div>
						</router-link>
					</van-col>
					<van-col span="8">
						<router-link tag="div" :to="util.getUrl({path: '/pages/statcenter/index'})" class="merchant-item">
							<div class="top">
								<i class="icon icon-rank c-info"></i>
							</div>
							<div class="bottom">
								<span>商户统计</span>
							</div>
						</router-link>
					</van-col>
					<van-col span="8">
						<router-link tag="div" :to="util.getUrl({path: '/pages/printer/list'})" class="merchant-item">
							<div class="top">
								<i class="icon icon-print c-warning"></i>
							</div>
							<div class="bottom">
								<span>打印机</span>
							</div>
						</router-link>
					</van-col>
				</van-row>
			</div>
			<div class="store-ads" v-if="ads.length > 0">
				<van-swipe :autoplay="3000">
					<van-swipe-item v-for="(item, index) in ads" :key="item.id">
						<div @click="util.jsUrl(item.link)">
							<img :src="util.tomedia(item.thumb)" />
						</div>
					</van-swipe-item>
				</van-swipe>
			</div>
		</div>
		<iloading v-if="showPreLoading"></iloading>
	</div>
</template>

<script>
	import publicHeader from "@/components/header"
	import publicFooter from "@/components/footer"
	import {getData} from "@/controller/funcCommon"
	export default {
		data() {
			return {
				store: {},
				stat: {},
				ads: {},
				showPreLoading: true,
				menufooter: {}
			}
		},
		components: {
			publicHeader,
			publicFooter
		},
		methods: {
			onLoad(){
				var that = this;
				getData({
					vue: this,
					url: 'manage/shop/index/index',
					data: {
						_account_perm: 1,
						menufooter: 1,
					},
					success: (res) => {
						that.store = res.store;
						that.stat = res.stat;
						if (res.ads){
							that.ads = res.ads;
						}
					}
				});
			}
		},
		mounted(){
			this.onLoad();
			this.util.imap();
			this.util.icloudapi();
		}
	}
</script>

<style>
	#store-home .content .store-data{
		width:33.3333333%;
		float:left;
		text-align:center;
		background:#fff;
		padding:15px 0;
	}
	#store-home .content .store-data .data-url{
		border-left:1px solid #e8e9eb;
		font-size:12px;
		overflow:hidden;
		display:block;
		width:100%;
		height:100%;
		color:#3d4145;
	}
	#store-home .content .store-data .data-url div:last-child{
		padding-top: 5px;
	}
	#store-home .content .store-data:first-child .data-url{
		border-left: 0px;
	}
	#store-home .content .group{
		margin: 10px 0;
		padding: 0 10px;
		background: #fff;
	}
	#store-home .content .group .van-row{
		text-align: center;
	}
	#store-home .content .group .merchant-item{
		margin-bottom: 20px;
	}
	#store-home .content .group .merchant-item .top{
		padding: 10px;
	}
	#store-home .content .group .merchant-item .top .icon{
		font-size: 40px;
	}
	#store-home .content .group .merchant-item .bottom{
		font-size: 14px;
		color: #7D8484;
	}
	#store-home .content .group .merchant-item .top .icon-shop{
		color:#F09A91;
	}
	#store-home .content .group .merchant-item .top .icon-goods{
		color:#E9972D;
	}
	#store-home .content .group .merchant-item .top .icon-recharge{
		color:#E9972D;
	}
	#store-home .content .group .merchant-item .top .icon-medal{
		color:#FFD700;
	}
	#store-home .content .store-ads{
		padding-bottom: 10px;
	}
	#store-home .content .store-ads .van-swipe-item img{
		width: 100%;
		height: 84px;
	}
</style>


